"use client"

import React from "react"
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts"

type Student = {
  comprehension: number
  attention: number
  focus: number
  retention: number
  assessment_score: number
}

export default function SkillBarChart({ data }: { data: Student[] }) {
  if (!data || data.length === 0) {
    return <p className="text-gray-500">No data available</p>
  }

  const avg = (arr: number[]) =>
    arr.length > 0 ? arr.reduce((a, b) => a + b, 0) / arr.length : 0

  const skills = ["comprehension", "attention", "focus", "retention"]

  const chartData = skills.map((skill) => ({
    skill,
    value: avg(
      data.map((d) => Number(d[skill as keyof Student]) || 0)
    ),
  }))

  // âœ… Filter out any accidental NaNs
  const safeData = chartData.filter((d) => !isNaN(d.value))

  return (
    <ResponsiveContainer width="100%" height={300}>
      <BarChart data={safeData} margin={{ top: 20, right: 30, left: 20, bottom: 5 }}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="skill" />
        <YAxis />
        <Tooltip />
        <Legend />
        <Bar dataKey="value" name="Avg Score" fill="#8884d8" />
      </BarChart>
    </ResponsiveContainer>
  )
}
