// app/components/OverviewStats.tsx
import { Card, CardContent } from "@/components/ui/card";

export default function OverviewStats({ metrics }: { metrics: any }) {
  if (!metrics) return null;

  return (
    <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
      <Card>
        <CardContent>
          <h3>Avg Score</h3>
          <p>{metrics.avg_score}</p>
        </CardContent>
      </Card>
      <Card>
        <CardContent>
          <h3>Avg Comprehension</h3>
          <p>{metrics.avg_comprehension}</p>
        </CardContent>
      </Card>
      <Card>
        <CardContent>
          <h3>Avg Attention</h3>
          <p>{metrics.avg_attention}</p>
        </CardContent>
      </Card>
      <Card>
        <CardContent>
          <h3>Avg Focus</h3>
          <p>{metrics.avg_focus}</p>
        </CardContent>
      </Card>
      <Card>
        <CardContent>
          <h3>Avg Retention</h3>
          <p>{metrics.avg_retention}</p>
        </CardContent>
      </Card>
      <Card>
        <CardContent>
          <h3>Avg Engagement Time</h3>
          <p>{metrics.avg_engagement_time} mins</p>
        </CardContent>
      </Card>
    </div>
  );
}
