import React from 'react'
import { ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts'


export default function AttentionScatter({ data }: { data: any[] }) {
const points = data.map((d) => ({ x: d.attention, y: d.assessment_score, name: d.name }))


return (
<div style={{ width: '100%', height: 300 }}>
<ResponsiveContainer>
<ScatterChart margin={{ top: 20, right: 20, bottom: 10, left: 0 }}>
<CartesianGrid />
<XAxis type="number" dataKey="x" name="Attention" />
<YAxis type="number" dataKey="y" name="Assessment" />
<Tooltip cursor={{ strokeDasharray: '3 3' }} formatter={(value: any) => value} />
<Scatter data={points} fill="#8884d8" />
</ScatterChart>
</ResponsiveContainer>
</div>
)
}