"use client"

import React from "react"
import {
  RadarChart,
  PolarGrid,
  PolarAngleAxis,
  PolarRadiusAxis,
  Radar,
  Legend,
} from "recharts"

type PersonaProfile = {
  persona: string
  comprehension: number
  attention: number
  focus: number
  retention: number
  assessment_score: number
  engagement_time: number
}

export default function PersonaRadar({ profiles }: { profiles: PersonaProfile[] }) {
  if (!profiles || profiles.length === 0) return <p>No persona data available</p>

  // Reformat for radar (skills only)
  const skills = ["comprehension", "attention", "focus", "retention", "assessment_score", "engagement_time"]

  const data = skills.map((skill) => {
    const entry: any = { skill }
    profiles.forEach((p) => {
      entry[p.persona] = p[skill as keyof PersonaProfile]
    })
    return entry
  })

  return (
    <RadarChart
      cx="50%"
      cy="50%"
      outerRadius="70%"
      width={350}
      height={300}
      data={data}
    >
      <PolarGrid />
      <PolarAngleAxis dataKey="skill" />
      <PolarRadiusAxis />
      {profiles.map((p, idx) => (
        <Radar
          key={idx}
          name={p.persona}
          dataKey={p.persona}
          stroke={`hsl(${idx * 60}, 70%, 50%)`}
          fill={`hsl(${idx * 60}, 70%, 50%)`}
          fillOpacity={0.3}
        />
      ))}
      <Legend />
    </RadarChart>
  )
}
