"use client"

import React from "react"

type Student = {
  student_id: number
  name: string
  class: string
  comprehension: number
  attention: number
  focus: number
  retention: number
  assessment_score: number
  engagement_time: number
  persona?: string
}

export default function Insights({ data }: { data: Student[] }) {
  if (!data || data.length === 0) {
    return <p className="text-gray-500">No data available</p>
  }

  // Compute averages
  const avg = (arr: number[]) =>
    arr.reduce((a, b) => a + b, 0) / (arr.length || 1)

  const avgAttention = avg(data.map((d) => d.attention))
  const avgScore = avg(data.map((d) => d.assessment_score))
  const avgRetention = avg(data.map((d) => d.retention))

  // Example insights
  const insights: string[] = [
    `Average attention is ${avgAttention.toFixed(1)}, while average score is ${avgScore.toFixed(
      1
    )}.`,
    avgAttention > 75
      ? "Students generally maintain high attention."
      : "Attention levels could be improved for better outcomes.",
    avgRetention < 70
      ? "Retention is relatively low â€” reinforcement strategies may help."
      : "Retention levels are strong overall.",
    data.some((d) => d.persona)
      ? `Personas identified: ${[
          ...new Set(data.map((d) => d.persona).filter(Boolean)),
        ].join(", ")}.`
      : "No persona clustering available.",
  ]

  return (
    <div>
      <h2 className="text-xl font-semibold mb-2">Key Insights</h2>
      <ul className="list-disc list-inside">
        {insights.map((i, idx) => (
          <li key={idx}>{i}</li>
        ))}
      </ul>
    </div>
  )
}
